import React, { useEffect, useState } from 'react';
import LayoutPage from '../Layout/layout';
import firebase from '../firebase';
import { NavLink, useHistory } from 'react-router-dom';
const TestList = () => {
  const [name, setName] = useState('');
  const [pass, setPass] = useState('');
  const [num, setNum] = useState('');
  const [type, setType] = useState('');
  const [dateStart, setDateStart] = useState('');
  const [hStart, setHStart] = useState('');
  const [mStart, setMStart] = useState('');
  const [dateFinsh, setDateFinish] = useState('');
  const [hFinish, setHFinish] = useState('');
  const [mFinish, setMFinish] = useState('');
  const [id, setId] = useState('');
  const [todoList, setTodoList] = useState([]);


  useEffect(() => {

    const getProduct = () => {
      const todoRef = firebase.database().ref('test');
      todoRef.on('value', (snapshot) => {
        const todos = snapshot.val();
        const data = [];
        for (let id in todos) {
          data.push({ id, ...todos[id] });
        }
        setTodoList(data);

      });
    }
    getProduct();

  }, [])
  const DeleteTest = (id) => {
    const addSt = firebase.database().ref('test/'+id)
    addSt.remove()
  }
  const edit = (todoList) => {
    let batdau = todoList.tgbatdau + '';
    let kethuc = todoList.tgketthuc + '';
    setName(todoList.baithi);
    setNum(todoList.socauhoi);
    setPass(todoList.matkhau);
    setDateStart(todoList.ngaybatdau);
    setHStart(batdau.substr(0, 2));
    setMStart(batdau.substr(3, 2));
    setDateFinish(todoList.ngayketthuc);
    setHFinish(kethuc.substr(0, 2));
    setMFinish(kethuc.substr(3, 2));
    setType(todoList.loai);
    setId(todoList.id);
  }
  const onChangeValue = (e) => {
    if (e.target.name === "name") {
      setName(e.target.value);
    }
    if (e.target.name === 'pass') {
      setPass(e.target.value);
    }
    if (e.target.name === 'so') {
      setNum(e.target.value);
    }
    if (e.target.name === 'loai') {
      setType(e.target.value);
    }
    if (e.target.name === 'batdau') {
      setDateStart(e.target.value);
    }
    if (e.target.name === 'gbd') {
      setHStart(e.target.value);
    }
    if (e.target.name === 'pbd') {
      setMStart(e.target.value);
    }
    if (e.target.name === 'ketthuc') {
      setDateFinish(e.target.value);
    }
    if (e.target.name === 'gkt') {
      setHFinish(e.target.value);
    } if(e.target.name === 'pkt'){
      setMFinish(e.target.value);
    }
  }
  const history = useHistory();
  const SubmitTest = () => {
    // const test = firebase.database().ref('test');
    let data = {
      baithi: name,
      matkhau: pass,
      socauhoi: num,
      ngaybatdau: dateStart,
      tgbatdau: hStart + ":" + mStart,
      ngayketthuc: dateFinsh,
      tgketthuc: hFinish + ":" + mFinish,
      loai: type
    }
    const test = firebase.database().ref('test/'+id).update(data);

    history.push('/test');
  }
  return (
    <LayoutPage>
      <div className="text-right">
        <NavLink to='/add-test' style={{ fontSize: 20 - 3 }}>Thêm bài thi</NavLink>
      </div>
      <h3>Danh sách bài thi</h3>
      <table className="table table-striped">
        <thead>
          <tr>
            <th>Tên bài thi</th>
            <th>Số câu hỏi</th>
            <th>Hình thức thi</th>
            <th>Thời gian thi</th>
            <th>#</th>
          </tr>
        </thead>
        <tbody>
          {todoList ? todoList.map((key, index) => (
            <tr>
              <td>{key.baithi}</td>
              <td>{key.socauhoi}</td>
              <td>{key.loai === 'TN' ? 'Trắc nghiệm' : 'Tự luận'}</td>
              <td>{key.tgbatdau + ' ' + key.ngaybatdau + ' - ' + key.tgketthuc + ' ' + key.ngayketthuc}</td>
              <td>
                <NavLink to={'/question/' + key.id}><button className="btn btn-primary">DS Câu hỏi</button></NavLink>
                <NavLink to={'#'}><button className="btn btn-secondary" data-toggle="modal" data-target="#myModal" onClick={() => edit(key)}>Sửa</button></NavLink>
                <NavLink to={'#'}><button className="btn btn-danger" onClick={()=>DeleteTest(key.id)}>Xóa</button></NavLink>
              </td>
            </tr>
          )) : null}

        </tbody>
      </table>
      <div id="myModal" className="modal fade" role="dialog">
        <div className="modal-dialog">
          {/* Modal content*/}
          <div className="modal-content">
            <div className="modal-header">
              <button type="button" className="close" data-dismiss="modal">×</button>
              <h4 className="modal-title">Sửa bài thi</h4>
            </div>
            <div className="modal-body">
              <div className="row">
                <div className="col-sm-12">
                  <div className="form-group">
                    <label htmlFor="usr">Tên bài thi:</label>
                    <input type="text" className="form-control" value={name} name="name" onChange={(e) => setName(e.target.value)} />
                  </div>
                  <div className="form-group">
                    <label htmlFor="usr">Mật khẩu bài thi:</label>
                    <input type="text" className="form-control" defaultValue={pass} required name="pass" onChange={onChangeValue} />
                  </div>
                  <div className="form-group">
                    <label htmlFor="usr">Số câu hỏi:</label>
                    <input type="text" className="form-control" defaultValue={num} name="so" onChange={onChangeValue} />
                  </div>
                  <div className="form-group">
                    <label htmlFor="usr">Hình thức thi:</label>
                    <select className="form-control" defaultValue={type} onChange={(e) => setType(e.target.value)} name="loai" onChange={onChangeValue}>
                      <option>Chọn</option>
                      <option value="TN">Trắc nghiệm</option>
                      <option value="TL">Tự luận</option>
                    </select>
                  </div>
                  <h4>Thời gian thi</h4>
                  <div className="row">
                    <div className="col-sm-6">
                      <h5>Bắt đầu</h5>
                      <div className="form-group">
                        <label htmlFor="usr">Ngày:</label>
                        <input type="text" className="form-control" defaultValue={dateStart} required name="batdau" onChange={onChangeValue} />
                      </div>
                      <div className="row">
                        <div className="col-sm-6">
                          <div className="form-group">
                            <label htmlFor="usr">Giờ:</label>
                            <input type="text" className="form-control" defaultValue={hStart} required name="gbd" onChange={onChangeValue} />
                          </div>
                        </div>
                        <div className="col-sm-6">
                          <div className="form-group">
                            <label htmlFor="usr">Phút:</label>
                            <input type="text" className="form-control" defaultValue={mStart} required name="pbd" onChange={onChangeValue} />
                          </div>
                        </div>
                      </div>
                    </div>

                    <div className="col-sm-6">
                      <h5>Kết thúc</h5>
                      <div className="form-group">
                        <label htmlFor="usr">Ngày:</label>
                        <input type="text" className="form-control" defaultValue={dateFinsh} required name="ketthuc" onChange={onChangeValue} />
                      </div>
                      <div className="row">
                        <div className="col-sm-6">
                          <div className="form-group">
                            <label htmlFor="usr">Giờ:</label>
                            <input type="text" className="form-control" defaultValue={hFinish} required name="gkt" onChange={onChangeValue} />
                          </div>
                        </div>
                        <div className="col-sm-6">
                          <div className="form-group">
                            <label htmlFor="usr">Phút:</label>
                            <input type="text" className="form-control" defaultValue={mFinish} required name="pkt" onChange={onChangeValue} />
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="modal-footer">
              <button type="button" className="btn btn-default" data-dismiss="modal">Thoát</button>
              <button type="button" className="btn btn-primary" data-dismiss="modal" onClick={SubmitTest}>Sửa bài thi</button>
            </div>
          </div>
        </div>
      </div>
    </LayoutPage>
  )
}
export default TestList;